@font-face {
  font-family: "Roboto Condensed";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/roboto-condensed-bold.woff2") format("woff2"), url("../fonts/roboto-condensed-bold.woff") format("woff");
  font-display: swap; }

@font-face {
  font-family: "Roboto Condensed";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/roboto-condensed-regular.woff2") format("woff2"), url("../fonts/roboto-condensed-regular.woff") format("woff");
  font-display: swap; }

@font-face {
  font-family: "Roboto Condensed";
  font-style: normal;
  font-weight: 300;
  src: url("../fonts/roboto-condensed-light.woff2") format("woff2"), url("../fonts/roboto-condensed-light.woff") format("woff");
  font-display: swap; }

@font-face {
  font-family: "Bebas Neue";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/bebas-neue-regular.woff2") format("woff2"), url("../fonts/bebas-neue-regular.woff") format("woff");
  font-display: swap; }

@font-face {
  font-family: "Bebas Neue";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/bebas-neue-bold.woff2") format("woff2"), url("../fonts/bebas-neue-bold.woff") format("woff");
  font-display: swap; }

html {
  box-sizing: border-box;
  scroll-behavior: smooth; }

*,
*::before,
*::after {
  box-sizing: inherit; }

img {
  max-width: 100%;
  object-fit: cover; }

a {
  text-decoration: none;
  color: inherit; }

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0; }

.wrapper {
  padding-left: 103px;
  padding-right: 103px;
  margin: 0 auto;
  max-width: 1366px; }
  @media (max-width: 1199px) {
    .wrapper {
      padding-left: 45px;
      padding-right: 45px; } }
  @media (max-width: 767px) {
    .wrapper {
      padding-left: 15px;
      padding-right: 15px; } }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%); }

.button {
  font-family: "Roboto Condensed", "Arial Narrow", sans-serif;
  font-size: 12px;
  line-height: 14px;
  font-weight: 700;
  text-align: center;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: #ffffff;
  border: 4px solid #ffffff;
  background-color: transparent;
  user-select: none;
  cursor: pointer; }
  .button:hover, .button:focus {
    opacity: 0.7; }
  .button:active {
    opacity: 0.5; }

.button--main-screen {
  padding: 15px 25px 19px;
  width: 410px;
  font-size: 18px;
  line-height: 21px;
  border: none; }

.page-body {
  margin: 0 auto;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  background-color: #ffffff; }

.page-header {
  padding: 20px 0 48px;
  color: #ffffff;
  background: conic-gradient(from 130.31deg at 16.03% 39.06%, #B80027 -46.69deg, #003D85 32.24deg, #B80027 313.31deg, #003D85 392.24deg); }
  @media (max-width: 1199px) {
    .page-header {
      padding: 30px 0 81px;
      background: conic-gradient(from 139.4deg at 20.7% 35.74%, #B80027 -46.69deg, #003D85 32.24deg, #B80027 313.31deg, #003D85 392.24deg); } }
  @media (max-width: 767px) {
    .page-header {
      padding: 10px 0 78px;
      background: conic-gradient(from 171.43deg at 35.31% 34.22%, #B80027 -46.69deg, #003D85 32.24deg, #B80027 313.31deg, #003D85 392.24deg); } }

.page-header__wrapper {
  display: flex; }

.page-header__navigation {
  display: flex;
  justify-content: space-between; }

.page-header__navigation ul {
  max-width: 850px;
  display: flex;
  justify-content: right;
  flex-wrap: wrap; }

.page-header__navigation li {
  margin: 7px 0 0 30px; }

.page-header__logo {
  margin: 10px 15px 0 0; }
  @media (max-width: 1199px) {
    .page-header__logo {
      width: 82px;
      height: 17px; } }
  @media (max-width: 767px) {
    .page-header__logo {
      width: 68px;
      height: 15px; } }

.page-header__link {
  font-family: "Bebas Neue", "Tahoma", sans-serif;
  font-size: 26px;
  line-height: 26px;
  font-weight: 700;
  letter-spacing: 0.03em;
  display: block;
  user-select: none; }
  @media (max-width: 1199px) {
    .page-header__link {
      font-size: 24px;
      line-height: 24px;
      letter-spacing: 0.02em; } }
  @media (max-width: 767px) {
    .page-header__link {
      font-size: 20px;
      line-height: 20px; } }
  .page-header__link:hover, .page-header__link:focus {
    opacity: 0.7; }
  .page-header__link:active {
    opacity: 0.5; }

.page-header__container {
  font-family: "Bebas Neue", "Tahoma", sans-serif;
  margin: 0 64px 0 16px;
  padding: 108px 0 47px 3px;
  display: flex;
  flex-direction: column;
  align-items: end;
  min-width: 380px; }
  @media (max-width: 1199px) {
    .page-header__container {
      margin: 0 30px 0 auto;
      padding: 71px 0 23px;
      width: calc(50% - 15px);
      min-width: 324px;
      max-width: 400px; } }
  @media (max-width: 767px) {
    .page-header__container {
      margin: 0 auto;
      padding: 71px 0 0;
      width: 100%;
      min-width: 290px;
      max-width: 380px; } }

.page-header__container p {
  font-size: 49px;
  line-height: 59px;
  font-weight: 400;
  letter-spacing: 0.03em;
  margin: 0;
  max-width: 375px;
  text-align: right; }
  @media (max-width: 1199px) {
    .page-header__container p {
      font-size: 34px;
      line-height: 41px; } }
  @media (max-width: 767px) {
    .page-header__container p {
      font-size: 28px;
      line-height: 34px;
      margin-bottom: 2px; } }

.page-header__container h1 {
  font-size: 109px;
  line-height: 109px;
  font-weight: 700;
  letter-spacing: 0.03em;
  text-align: right;
  text-shadow: 0 -2px 0 #ffffff, 0 -2px 0 #ffffff, 0 2px 0 #ffffff, 0 2px 0 #ffffff, -2px 0 0 #ffffff, 2px 0 0 #ffffff, -2px 0 0 #ffffff, 2px 0 0 #ffffff, -1px -2px 0 #ffffff, 1px -2px 0 #ffffff, -1px 2px 0 #ffffff, 1px 2px 0 #ffffff, -2px -1px 0 #ffffff, 2px -1px 0 #ffffff, -2px 1px 0 #ffffff, 2px 1px 0 #ffffff, -2px -2px 0 #ffffff, 2px -2px 0 #ffffff, -2px 2px 0 #ffffff, 2px 2px 0 #ffffff, -2px -2px 0 #ffffff, 2px -2px 0 #ffffff, -2px 2px 0 #ffffff, 2px 2px 0 #ffffff;
  margin: 0 0 15px;
  max-width: 300px; }
  @media (max-width: 1199px) {
    .page-header__container h1 {
      font-size: 86px;
      line-height: 86px;
      margin: -4px 0 15px;
      max-width: 235px; } }
  @media (max-width: 767px) {
    .page-header__container h1 {
      font-size: 78px;
      line-height: 78px;
      max-width: 215px;
      margin: 0 0 73px; } }

.page-header__container ul {
  font-family: "Roboto Condensed", "Arial Narrow", sans-serif;
  font-size: 14px;
  line-height: 21px;
  font-weight: 400;
  align-self: flex-start;
  text-align: right;
  margin: -98px 0 83px;
  max-width: 115px; }
  @media (max-width: 1199px) {
    .page-header__container ul {
      margin: -88px 0 57px; } }
  @media (max-width: 767px) {
    .page-header__container ul {
      margin: -143px 0 55px; } }

.page-header__button {
  margin-top: auto;
  padding: 21px 24px 18px;
  width: 234px; }
  @media (max-width: 1199px) {
    .page-header__button {
      width: 187px; } }
  @media (max-width: 767px) {
    .page-header__button {
      padding: 22px 24px 19px;
      width: 100%; } }

@media (max-width: 1199px) {
  .page-header__image {
    width: calc(50% - 15px);
    margin: 21px 0 0; } }

@media (max-width: 767px) {
  .page-header__image {
    display: none; } }

.page-header__image img {
  display: block;
  height: 100%; }
  @media (max-width: 1199px) {
    .page-header__image img {
      margin: 0 0 0 auto; } }

.page-header__social-list {
  font-family: "Roboto Condensed", "Arial Narrow", sans-serif;
  font-size: 12px;
  line-height: 14px;
  font-weight: 400;
  margin: 0 0 44px auto;
  max-width: 30px;
  align-self: flex-end;
  text-align: right; }
  @media (max-width: 1199px) {
    .page-header__social-list {
      display: none; } }

.page-header__social-item {
  margin-top: 20px; }

.page-main {
  flex-grow: 1; }

.page-footer {
  padding: 50px 0 55px; }

.page-footer__wrapper {
  display: flex;
  justify-content: space-between;
  gap: 5px; }

.page-footer__logo {
  display: block;
  margin-bottom: 25px; }

.page-footer__social-list {
  display: flex;
  flex-wrap: wrap;
  max-width: 220px; }

.page-footer__social-item {
  margin: 0 5px 10px 10px; }

.page-footer__social-link {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 40px;
  height: 40px; }
  .page-footer__social-link:hover, .page-footer__social-link:focus {
    opacity: 0.7; }
  .page-footer__social-link:active {
    opacity: 0.5; }

.page-footer__list {
  display: flex;
  justify-content: space-between;
  gap: 5px;
  max-width: 845px;
  flex-grow: 1;
  padding-top: 4px; }

